/* SHARE POPUP */

.share__button-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  gap: 30px;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}

.share__button {
  border: none;
  width: 46px;
  height: 46px;
  border-radius: 8px;
}

.share__link {
  padding: 10px;
  border-radius: 4px;
  width: 270px;
  margin: 30px auto;
}

.share__link-input {
  background: transparent;
  border: transparent;
  color: #e9ecef;
}

/* VERIFICATION */
.input_error {
  font-size: 12px;
  color: #dc3545;
  margin-bottom: 0;
}

.input_error_active {
  opacity: 1;
